.header {
  display: flex;
  justify-content: flex-end;
  background-color: rgb(248, 178, 131);
  padding: 30px 0 30px 0;
}
.header__user {
  color: white;
  font-size: 1.2em;
}
.header__button {
  border-color: antiquewhite;
  font-size: 1.2em;
  margin: 0px 20px 0 0;
  padding: 10px 15px;
  border-radius: 10px;
  text-decoration: none;
  color: white;
  border: 2px solid;
}
.header__menu {
  display: flex;
  align-items: center;
  z-index: 3;
}
@media (max-width: 375px) {
  .header__button {
    font-size: 1em;
  }
  .header__user {
    font-size: 1em;
  }
}
@media (max-width: 767px) {
  .header__button {
    margin-right: 20px;
  }
  .header__menu {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: auto;
    background-color: rgb(248, 178, 131);
    transform: translateY(-100%);
    transition-duration: 1s;
    z-index: 2;
  }
  .header__menu.active {
    transform: translateY(0%);
  }
  .header__menu {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .icon-menu {
    display: block;
    position: absolute;
    top: 18px;
    right: 10px;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5;
  }
  .menu__body {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: auto;
    background-color: #4d4959;
    padding-top: 150px;
    transform: translateY(-100%);
    transition-duration: 1s;
    z-index: 1;
  }
  .icon-menu span {
    -webkit-text-decoration: 1s;
    text-decoration: 1s;
    top: 8px;
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #4d4959;
    transition-duration: 1s;
  }
  .icon-menu span:first-child {
    top: 0;
  }
  .icon-menu span:last-child {
    top: auto;
    bottom: 0;
  }
}
.icon-menu.active span {
  background-color: #fff;

  transform: scale(0);
}
.icon-menu.active span:first-child {
  transform: rotate(-45deg);
  top: 8px;
}

.icon-menu.active span:last-child {
  transform: rotate(45deg);
  bottom: 8px;
}
