.sing__button {
    border-color: antiquewhite;
    font-size: 1.2em;
    color: white;
    border: 2px solid;
    background-color: rgb(248, 178, 131);
    border-radius: 25px;
    padding: 10px 15px;
    cursor: pointer;
    text-decoration: none;
    margin: 10px; 
    display: flex;
    justify-content: center;
}
.wrapper:first-child{
    margin-top: 50px;
}

.sing__container{
   
    text-align: center;
    margin: 0 auto;
}

.sing__container h1{
margin: -15% 0 0 0 ;
font-size: 1.5em;
}
.sing__container img{
    width: 300px;
}
.wrapper{
    position: relative;
    width: 300px;
    margin: 0 auto;
}
.sing__arrow{
    position: absolute;
    right: 50px;
    top: 15px;
    color:white
}